@use "../../scss/" as *;

.patient-prescription {
    background-color: white;
    &-container {
        padding: 50px;
        &-header {
            @include flex(center, space-between);
            .patient-prescription-title {
                padding: 20px 20px 20px 0;
            }
            .btn-back {
                background-color: #fafafa;
                border: 1px solid #979696b4;
                color: #576370;
                padding: 10px;
                border-radius: 5px;
            }
        }
        .patient-prescription-panel {
            // @include flex(center, space-between)
            display: grid;
            grid-template-columns: 20% 80%;
            &-left {
                border: 0.2px solid #dcdcdc;
                border-right: none;
                width: 100%;
                &-title {
                    background-color: #00A19D;
                    text-align: center;
                    font-size: 20px;
                    padding: 10px;
                    color: white;
                }
                &-table {
                    padding: 10px;
                    .patient-id,
                    .patient-name,
                    .patient-gender,
                    .patient-dob,
                    .patient-address {
                        color: #576370;
                        height: 50px;
                        .content {
                            padding: 10px;
                        }
                    }
                }
            }
            &-right {
                border: 0.2px solid #dcdcdc;
                .patient-prescription {
                    &-bar {
                        &-title {
                            padding: 10px;
                            text-align: center;
                            font-size: 20px;
                            background-color: #00A19D;
                            font-weight: 600;
                            color: white;
                        }
                        .add-prescription {
                            font-size: 18px;
                            padding: 6px 19px 6px 19px;
                            background-color: #fafafa;
                            border-radius: 5px;
                            margin-left: 20px;
                            margin-top: 20px;
                            border: 1px solid #f0f0f0;
                            color: #576370;
                        }
                    }
                    &-list {
                        padding: 20px;
                        .prescription-name {
                            cursor: pointer;
                            td {
                                width: 360px;
                                border: 1px solid #dcdcdc;
                                background-color: #fafafa;
                                border-radius: 5px;
                                font-size: 16px;
                                padding: 6px;
                            }
                            .createdDate {
                                font-size: 12px;
                                color: #dcdcdc;
                                margin-top: 3px;
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 1024px) {
    .patient-prescription {
        &-container {
            padding: 20px;
            .patient-prescription-panel {
                grid-template-columns: 35% 65%;
                &-left {
                    &-title {
                        font-size: 24px;
                    }
                }
                &-right {
                    .patient-prescription {
                        &-bar {
                            &-title {
                                font-size: 24px;
                            }
                            .add-prescription {
                                font-size: 16px;
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .patient-prescription {
        &-container {
            padding: 10px;
            &-header {
                .patient-prescription-title {
                    padding: 10px 10px 10px 0;
                    font-size: 26px;
                }
            }
            .patient-prescription-panel {
                display: flex;
                flex-direction: column;
                border: none;
                &-left {
                    border: none;
                    &-title {
                        font-size: 20px;
                    }
                    &-table {
                        .patient-id,
                        .patient-name,
                        .patient-gender,
                        .patient-dob,
                        .patient-address {
                            height: 20px;
                        }
                    }
                }
                &-right {
                    border: none;
                    .patient-prescription {
                        &-bar {
                            &-title {
                                font-size: 20px;
                            }
                            .add-prescription {
                                margin-left: 0;
                            }
                        }
                        &-list {
                            margin-top: 10px;
                            padding: 0;
                        }
                    }
                }
            }
        }
    }
}
