@use "../../scss/" as *;

.prescription {
    padding: 32px;
    background-color: rgba(128, 128, 128, 0.021);
    &-container {
        .prescription-header {
            padding: 20px;
            &-title {
                font-size: 30px;
            }
        }
    }
    &-panel {
        display: grid;
        grid-template-columns: 49% 49%;
        grid-gap: 5px;
        // padding: 20px;
        &-table {
            padding: 10px;
            border-right: 1px dashed rgba(0, 0, 0, 0.219);
            &-header {
                &-title {
                    text-align: center;
                    font-size: 20px;
                }
            }
            .prescription-name,
            .prescription-doctor,
            .prescription-create-date,
            .patient-name {
                &-title {
                    font-size: 14px;
                    width: 20%;
                }
                &-value {
                    width: 80%;
                }
                .input {
                    width: 90%;
                    padding: 10px;
                    font-size: 14px;
                    width: 100%;
                    align-items: center;
                    background-color: hsl(0, 0%, 100%);
                    border-color: hsl(0, 0%, 80%);
                    border-radius: 4px;
                    border-style: solid;
                    border-width: 1px;
                    cursor: default;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    min-height: 38px;
                    outline: 0 !important;
                    position: relative;
                    transition: all 100ms;
                    box-sizing: border-box;
                    cursor: pointer;
                }
            }
        }
        .medication-dose {
            padding: 10px;
            border-left: 1px dashed rgba(0, 0, 0, 0.219);
            &-header {
                text-align: center;
                &-title {
                    height: 50px;
                    font-size: 20px;
                }
            }
            .medication-class,
            .medication-name,
            .route,
            .schedule {
                &-title {
                    font-size: 14px;
                    width: 20%;
                }
                &-value {
                    width: 80%;
                }
                .input {
                    padding: 10px;
                    width: 100%;
                    font-size: 14px;
                    align-items: center;
                    background-color: hsl(0, 0%, 100%);
                    border-color: hsl(0, 0%, 80%);
                    border-radius: 4px;
                    border-style: solid;
                    border-width: 1px;
                    cursor: default;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    min-height: 38px;
                    outline: 0 !important;
                    position: relative;
                    transition: all 100ms;
                    box-sizing: border-box;
                    cursor: pointer;
                }
            }
            .medication-name {
                .input {
                    margin-top: 10px;
                }
            }
            .route {
                .input {
                    margin-top: 10px;
                    margin-bottom: 10px;
                }
            }
        }
    }
    .btn {
        padding: 20px;
        @include flex(center, center);
        .add-prescription {
            background-color: #00a19d;
            color: #fff;
            padding: 10px;
            font-size: 14px;
            border-radius: 5px;
            margin: auto 10px;
        }
    }
    .medication-dose-table {
        width: 100%;
        border: 0.5px solid;
        &-body {
            .medication-dose-table-header {
                &-title {
                    font-size: 14px;
                    text-align: center;
                    height: 40px;
                    font-weight: 600;
                }
            }
            .medication-detail-title {
                .medication-dose-class-title,
                .medication-dose-schedule-title,
                .medication-dose-route-title,
                .medication-dose-name-title {
                    border: 0.5px solid;
                    font-size: 14px;
                    height: 40px;
                }
            }
            .medication-dose-content {
                .medication-dose-class-value,
                .medication-dose-name-value,
                .medication-dose-route-value,
                .medication-dose-schedule-value {
                    text-align: center;
                    border: 0.5px solid;
                    height: 40px;
                    font-size: 14px;
                }
            }
        }
    }
}
@media screen and (max-width: 1024px) {
    .prescription {
        &-container {
            .prescription-header {
                &-title {
                    text-align: center;
                }
            }
        }
        &-panel {
            grid-gap: 20px;
            grid-template-columns: auto;
            &-table {
                border: none;
                padding-right: 0;
                &-header {
                    &-title {
                        height: 40px;
                    }
                }
                .prescription-name,
                .prescription-doctor {
                    &-title {
                        width: 20%;
                    }
                    &-value {
                        width: 90%;
                    }
                    .input {
                        padding: 6px;
                    }
                }
                .prescription-doctor {
                    margin: 10px 0;
                }
            }
            .medication-dose {
                border: none;
                &-title {
                    text-align: center;
                    margin: 20px auto;
                }
                padding-left: 0;
                .medication-class,
                .medication-name,
                .route,
                .schedule {
                    .input {
                        padding: 1px;
                    }
                }
                .medication-name {
                    .input {
                        margin-top: 0;
                    }
                }
                .route {
                    .input {
                        margin-top: 0;
                        margin-bottom: 0;
                    }
                }
            }
        }
        .btn {
            justify-content: flex-end;
            .add-prescription {
                margin: 0 20px;
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .prescription {
        height: 100%;
        .btn {
            justify-content: center;
        }
    }
}
