@use "../../scss/" as *;

.patient-view {
    background-color: white;
    &-container {
        padding: 50px;
        &-header {
            @include flex(center, space-between);
            &-bar {
                @include flex(center, space-between);
                .patient-view-title {
                    padding: 20px 20px 20px 0;
                }
                .btn-to-prescription,
                .change-pass {
                    background-color: #fafafa;
                    border: 1px solid #979696b4;
                    color: #576370;
                    padding: 10px;
                    border-radius: 5px;
                    margin-right: 10px;
                }
            }
            .btn-back {
                background-color: #fafafa;
                border: 1px solid #979696b4;
                color: #576370;
                padding: 10px;
                border-radius: 5px;
            }
        }
        .patient-view-panel {
            display: grid;
            grid-template-columns: 20% 60% 20%;
            &-left {
                border: 0.2px solid #dcdcdc;
                border-right: none;
                width: 100%;
                &-title {
                    background-color: #014ed5;
                    text-align: center;
                    font-size: 18px;
                    padding: 10px;
                    color: white;
                }
                &-table {
                    padding: 10px;
                    .patient-id,
                    .patient-name,
                    .patient-gender,
                    .patient-dob,
                    .patient-address {
                        color: #576370;
                        height: 50px;
                        .content {
                            padding: 10px;
                        }
                    }
                }
            }
            &-right {
                border: 0.2px solid #dcdcdc;
                .doctor {
                    &-bar {
                        &-title {
                            padding: 10px;
                            text-align: center;
                            font-size: 18px;
                            background-color: #014ed5;
                            font-weight: 600;
                            color: white;
                        }
                    }
                    &-list {
                        padding: 20px;
                        .doctor-name {
                            td {
                                min-width: 200px;
                                width: fit-content;
                                border: 1px solid #dcdcdc;
                                background-color: #fafafa;
                                border-radius: 5px;
                                font-size: 16px;
                                padding: 16px;
                            }
                            .active {
                                color: #fff;
                                border-color: #014ed5;
                                border-width: 2px;
                                color: black;
                            }
                        }
                    }
                }
            }
            &-middle {
                border: 0.2px solid #dcdcdc;
                .doctor {
                    &-bar {
                        &-title {
                            padding: 10px;
                            text-align: center;
                            font-size: 18px;
                            background-color: #014ed5;
                            font-weight: 600;
                            color: white;
                        }
                    }
                    &-list {
                        padding: 20px;
                        .doctor-name {
                            td {
                                min-width: 200px;
                                width: fit-content;
                                border: 1px solid #dcdcdc;
                                background-color: #fafafa;
                                border-radius: 5px;
                                font-size: 16px;
                                padding: 16px;
                            }
                            .active {
                                color: #fff;
                                border-color: #014ed5;
                                border-width: 2px;
                                color: black;
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 1024px) {
    .patient-view {
        &-container {
            padding: 20px;
            .patient-view-panel {
                grid-template-columns: 35% 65%;
                &-left {
                    &-title {
                        font-size: 16px;
                    }
                }
                &-right {
                    .doctor {
                        &-bar {
                            &-title {
                                font-size: 16px;
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .patient-view {
        &-container {
            padding: 10px;
            &-header {
                .patient-view-title {
                    padding: 10px 10px 10px 0;
                    font-size: 20px;
                }
            }
            .patient-view-panel {
                display: flex;
                flex-direction: column;
                border: none;
                &-left {
                    border: none;
                    &-title {
                        font-size: 18px;
                    }
                    &-table {
                        .patient-id,
                        .patient-name,
                        .patient-gender,
                        .patient-dob,
                        .patient-address {
                            height: 20px;
                        }
                    }
                }
                &-right {
                    border: none;
                    .doctor {
                        &-bar {
                            &-title {
                                font-size: 18px;
                            }
                            .add-prescription {
                                margin-left: 0;
                            }
                        }
                        &-list {
                            margin-top: 10px;
                            padding: 0;
                        }
                    }
                }
            }
        }
    }
}
