@use "../../scss/" as *;

.edit-patient {
    padding: 20px;
    &-title {
        font-size: 26px;
    }
    &-body {
        padding: 20px;
        &-name-title,
        &-id-title,
        &-current-password-title,
        &-new-password-title,
        &-confirm-new-password-title {
            margin-bottom: 4px;
            margin-top: 20px;
            font-size: 12px;
            color: #3d3d3d;
        }
        .input {
            width: 100%;
            align-items: center;
            background-color: #fff;
            border: 1px solid #ccc;
            border-radius: 4px;
            box-sizing: border-box;
            cursor: default;
            display: flex;
            flex-wrap: wrap;
            font-size: 14px;
            justify-content: space-between;
            min-height: 38px;
            outline: 0 !important;
            padding: 10px;
            position: relative;
            transition: all 0.1s;
            width: 320px;
        }
        .btn {
            padding-top: 32px;
            .back-btn,
            .edit-btn {
                background-color: #00a19d;
                border-radius: 4px;
                color: #fff;
                font-size: 14px;
                padding: 10px 20px;
            }
            .edit-btn {
                margin-left: 20px;
            }
        }
    }
}
