@use "./scss/"as *;

.font-gg {
    font-family: 'Roboto Slab', serif;
}

.main-button {
    background-color: #014ed5;
    font-size: medium;
    padding: 10px 20px;
    border-radius: 4px;
    color: white;
}

.sec-button {
    border-radius: 4px;
    border: 1px solid #f0f0f0;
    background-color: #fafafa;
    font-size: medium;
    color: #576370;
    padding: 10px 20px;
}

.logout-button {
    height: fit-content;
    color: #585858;
    background-color: #D1D1D1;
    padding: 8px 16px 8px 16px;
    border-radius: 4px;
    font-size: medium;
}

.button-h {
    &-m {
        height: 40px !important;
    }
}

.margin {
    &-t {
        &-8 {
            margin-top: 8px;
        }
    }
    &-b {
        &-8 {
            margin-bottom: 8px;
        }
    }
    &-r {
        &-8 {
            margin-right: 8px;
        }
        &-16 {
            margin-right: 16px;
        }
    }
    &-l {
        &-8 {
            margin-left: 8px;
        }
        &-16 {
            margin-left: 16px;
        }
    }
}