@use "../../scss/" as *;

.patient {
    .header-view {
        background-color: #F3F6FD;
        padding: 16px
    }
    &-container {
        .logout {
            @include flex(center, end);
            padding: 10px;
            font-size: 18px;
        }
        .desc-title {
            width: fit-content;
        }
        .patient-area {
            .patient-panel {
                background-color: lightgrey;
                width: 100%;
                height: 100%;
            }
            .patient-list {
                padding: 16px 32px 16px 32px;
                width: 100%;
                height: 100%;
                &-bar {
                    @include flex(center, start);
                    padding: 10px 0 20px 0;
                    &-title {
                        font-weight: 600;
                        font-size: 18px;
                    }
                    &-icon {
                        padding: 10px;
                        display: none;
                        cursor: pointer;
                        border-radius: 5px;
                        margin-left: 10px;
                        background-color: #00a19d;
                        color: white;
                    }
                }
                .patient-infos {
                    display: grid;
                    grid-template-columns: repeat(auto-fit, minmax(250px, 350px));
                    grid-gap: 10px;
                    .patient-info {
                        background-color: #fafafa;
                        border: 1px solid #f0f0f0;
                        border-radius: 4px;
                        @include flex(center, space-between);
                        .patient-desc {
                            padding: 10px;
                            .patient-name {
                                font-weight: 600;
                                font-size: 20px;
                                color: #576370;
                            }
                            .patient-gender,
                            .patient-dob,
                            .patient-address,
                            .patient-room,
                            .doctor,
                            .doctor-exp-date {
                                padding: 5px;
                                .title {
                                    font-size: 14px;
                                }
                                .content {
                                    font-size: 14px;
                                    color: #576370;
                                }
                            }
                        }
                        .patient-avatar {
                            padding: 10px;
                            text-align: center;
                            .patient-img {
                                background-color: #dcdcdc;
                                width: 100px;
                                height: 120px;
                            }
                            .patient-status {
                                font-weight: 600;
                                font-size: 16px;
                                margin: 10px 0;
                                color: #576370;
                            }
                            .patient-detail {
                                padding: 10px;
                                background-color: #00a19d;
                                font-weight: 500;
                                font-size: 14px;
                                border-radius: 5px;
                                color: white;
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 1024px) {
    .patient {
        &-container {
            .logout {
                .btn-logout {
                    font-size: 14px;
                }
                .btn-change-password{
                    font-size: 14px;
                }
            }
            
