@use "../../scss/"as *;

.bg-view-img {
    //background-image: url(../../resources/26358.jpg);
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
}

.content-view {
    margin: auto;
    background-color: white;
    //border: 1px solid rgb(176, 176, 176);
    width: fit-content;
    align-items: center;
    padding-top: 32px;
    padding-left: 32px;
    padding-right: 32px;
    padding-bottom: 32px;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-box-shadow: 2px 2px 32px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 2px 2px 32px 0px rgba(0, 0, 0, 0.2);
    border-radius: 10px;

    .button {
        border-radius: 4px;
        background-color: #014ed5;
        font-size: medium;
        padding: 16px 16px 16px 16px;
        color: white;
        margin-right: 8px;
        //box-shadow: 3px 3px #777070;
        //border: 1px solid #1B1A17;
        width: 160px;
        height: 50px;
        // -webkit-box-shadow: 2px 2px 32px 0px rgba(0, 0, 0, 0.2);
        // box-shadow: 2px 2px 32px 0px rgba(0, 0, 0, 0.2);
        margin-top: 16px;
    }

    .new-account {
        color: #343434 !important;
        font-size: small !important;
        border: 1px solid #f0f0f0 !important;
        background-color: #fafafa !important;
    }

    .text-input {
        padding: 10px;
        background-color: hsl(0, 0%, 100%);
        border-radius: 4px;
        border: 1px solid hsl(0, 0%, 80%);
        display: flex;
        justify-content: space-between;
        outline: 0 !important;
        transition: all 100ms;
        height: 50px;
        margin-top: 16px;
        margin: 16px auto;
        width: 260px;
        text-align: center;
        font-size: large;
    }

    .desc-text {
        font-size: small;
        color: #858585;
        margin-top: 8px;
        margin-bottom: 32px;
    }

    .err-text {
        font-size: small;
        color: red;
        margin-top: 4px;
        margin-bottom: 32px;
    }

    .title-text {
        font-size: large;
    }

}