@use "../../scss/" as *;

.prescription-detail {
    background-color: rgba(128, 128, 128, 0.014);
    &-container {
        padding: 20px;
        .prescription-detail-header {
            @include flex(center, space-between);
            &-title {
                font-size: 36px;
            }
            .btn-back {
                background-color: #f0f0f0;
                color: #000000;
                padding: 10px;
                font-size: 14px;
                border-radius: 5px;
            }
        }
        .prescription-detail-table {
            margin-top: 50px;
            width: 100%;
            font-size: 14px;
            text-align: center;
            border: 1px groove;
            .prescription-detail-id,
            .prescription-detail-name,
            .prescription-detail-doctor,
            .prescription-detail-date,
            .prescription-detail-patient-name,
            .prescription-detail-medication-dose {
                &-title {
                    border: 1px groove;
                    color: #000;
                    background-color: rgba(128, 128, 128, 0.3);
                    height: 50px;
                }
                &-content {
                    border: 1px groove;
                }
            }
            .prescription-detail-medication-dose {
                &-content {
                    .medication-detail {
                        width: 100%;
                        &-title {
                            .medication-detail-class-title,
                            .medication-detail-name-title,
                            .medication-detail-route-title,
                            .medication-detail-schedule-title {
                                border: 1px groove;
                                color: #000;
                                background-color: rgba(128, 128, 128, 0.3);
                                height: 50px;
                            }
                        }
                        &-content {
                            .medication-detail-class-value,
                            .medication-detail-name-value,
                            .medication-detail-route-value,
                            .medication-detail-schedule-value {
                                border: 1px groove;
                                height: 40px;
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 1024px) {
    .prescription-detail {
        &-container {
            padding: 10px;
            height: 100%;
            .prescription-detail-header {
                @include flex(center, space-between);
                &-title {
                    font-size: 24px;
                }
                .btn-back {
                    font-size: 14px;
                }
            }
            .prescription-detail-table {
                margin-top: 20px;
                .prescription-detail-id,
                .prescription-detail-name,
                .prescription-detail-doctor,
                .prescription-detail-date,
                .prescription-detail-patient-name,
                .prescription-detail-medication-dose {
                    &-title {
                        height: 20px;
                        font-size: 14px;
                    }
                    &-content {
                        font-size: 14px;
                    }
                }
                .prescription-detail-medication-dose {
                    &-content {
                        .medication-detail {
                            &-title {
                                .medication-detail-class-title,
                                .medication-detail-name-title,
                                .medication-detail-route-title,
                                .medication-detail-schedule-title {
                                    height: 30px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .prescription-detail {
        &-container {
            padding: 0;
            .prescription-detail-header {
                padding: 10px;
                // &-title {
                //     font-size: 24px;
                // }
                // .btn-back {
                //     font-size: 14px;
                // }
            }
            .prescription-detail-table {
                margin-top: 20px;
                .prescription-detail-id,
                .prescription-detail-name,
                .prescription-detail-doctor,
                .prescription-detail-date,
                .prescription-detail-patient-name,
                .prescription-detail-medication-dose {
                    &-title {
                        font-size: 14px;
                    }
                    &-content {
                        font-size: 14px;
                    }
                }
                .prescription-detail-medication-dose {
                    &-content {
                        .medication-detail {
                            &-title {
                                .medication-detail-class-title,
                                .medication-detail-name-title,
                                .medication-detail-route-title,
                                .medication-detail-schedule-title {
                                    font-size: 14px;
                                }
                            }
                            &-content {
                                .medication-detail-class-value,
                                .medication-detail-name-value,
                                .medication-detail-route-value,
                                .medication-detail-schedule-value {
                                    font-size: 14px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
