@use "../../scss/" as *;

.patient-prescription-detail {
    background-color: rgba(128, 128, 128, 0.014);
    &-container {
        padding: 50px;
        .patient-prescription-body {
            display: grid;
            grid-template-columns: auto auto auto;
            .patient-prescription-content {
                .patient-prescription-detail-header {
                    // @include flex(center, space-between);
                    &-title {
                        font-size: 20px;
                        font-weight: 600;
                        padding: 20px;
                        text-align: center;
                    }
                    .btn-back {
                        background-color: rgba(128, 128, 128, 0.342);
                        color: #0f0f0f;
                        padding: 10px;
                        font-size: 14px;
                        border-radius: 5px;
                        float: right;
                        margin-right: 10px;
                    }
                }
                border: 1px dashed #0f0f0f;
                &-info {
                    padding: 10px;
                    &-title {
                        padding: 0 0 10px 20px;
                        font-size: 16px;
                        font-weight: 600;
                    }
                    &-id,
                    // &-name,
                    &-DOB,
                    &-address,
                    &-gender,
                    &-assigned-doctor {
                        font-size: 14px;
                        padding: 10px 20px;
                        display: flex;
                        &-label {
                            text-transform: uppercase;
                        }
                        &-value {
                            margin-left: 10px;
                        }
                    }
                }
                .line{
                    margin: 0 auto;
                    width: 95%;
                    border: 1px dashed;
                }
                .patient-prescription-detail-table {
                    padding: 20px;
                    width: 100%;
                    font-size: 14px;
                    text-align: center;
                    // border: 1px groove;
                    .patient-prescription-detail{
                        border: none;
                        height: 40px;
                        &-title{
                            font-size: 14px;
                            font-weight: 600;
                        }
                    }
                    // .patient-prescription-detail-id,
                    // .patient-prescription-detail-name,
                    .patient-prescription-detail-date,
                    // .patient-prescription-detail-patient-name,
                    // .patient-prescription-detail-doctor,
                    .patient-prescription-detail-medication-dose {
                        &-title {
                            color: #0f0f0f;
                            border: 1px groove;
                            // background-color: #00a19d;
                            height: 40px;
                        }
                        &-content {
                            border: 1px groove;
                        }
                    }
                    .patient-prescription-detail-medication-dose {
                        &-content {
                            .patient-medication-detail {
                                width: 100%;
                                &-title {
                                    .patient-medication-detail-class-title,
                                    .patient-medication-detail-name-title,
                                    .patient-medication-detail-route-title,
                                    .patient-medication-detail-schedule-title {
                                        color: #0f0f0f;
                                        // background-color: #00a19d;
                                        height: 40px;
                                        border: 1px groove;
                                    }
                                }
                                &-content {
                                    .patient-medication-detail-class-value,
                                    .patient-medication-detail-name-value,
                                    .patient-medication-detail-route-value,
                                    .patient-medication-detail-schedule-value {
                                        border: 0.5px groove;
                                        height: 40px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 1024px) {
    .patient-prescription-detail {
        &-container {
            padding: 10px;
            height: 100%;
            .patient-prescription-detail-header {
                @include flex(center, space-between);
                &-title {
                    font-size: 24px;
                }
                .btn-back {
                    font-size: 14px;
                }
            }
            .patient-prescription-detail-table {
                margin-top: 20px;
                .patient-prescription-detail-id,
                .patient-prescription-detail-name,
                // .patient-prescription-detail-doctor,
                .patient-prescription-detail-date,
                // .patient-prescription-detail-patient-name,
                .patient-prescription-detail-medication-dose {
                    &-title {
                        height: 20px;
                        font-size: 14px;
                    }
                    &-content {
                        font-size: 14px;
                    }
                }
                .patient-prescription-detail-medication-dose {
                    &-content {
                        .patient-medication-detail {
                            &-title {
                                .patient-medication-detail-class-title,
                                .patient-medication-detail-name-title,
                                .patient-medication-detail-route-title,
                                .patient-medication-detail-schedule-title {
                                    height: 30px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .patient-prescription-detail {
        &-container {
            padding: 0;
            .patient-prescription-detail-header {
                padding: 10px;
            }
            .patient-prescription-detail-table {
                margin-top: 20px;
                .patient-prescription-detail-id,
                .patient-prescription-detail-name,
                .patient-prescription-detail-doctor,
                .patient-prescription-detail-date,
                .patient-prescription-detail-patient-name,
                .patient-prescription-detail-medication-dose {
                    &-title {
                        font-size: 14px;
                    }
                    &-content {
                        font-size: 14px;
                    }
                }
                .patient-prescription-detail-medication-dose {
                    &-content {
                        .patient-medication-detail {
                            &-title {
                                .patient-medication-detail-class-title,
                                .patient-medication-detail-name-title,
                                .patient-medication-detail-route-title,
                                .patient-medication-detail-schedule-title {
                                    font-size: 14px;
                                }
                            }
                            &-content {
                                .patient-medication-detail-class-value,
                                .patient-medication-detail-name-value,
                                .patient-medication-detail-route-value,
                                .patient-medication-detail-schedule-value {
                                    font-size: 14px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
